* {
  font-family: Futura, Helvetica, sans-serif;
  box-sizing: border-box;
  color: white;
}

body {
  margin: 0;
  background-color: black;
}

.flex-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  justify-content: space-between;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  width: 100%;
  color: white;
}

.header {
  font-size: 5rem;
  text-shadow: 2px 4px 4px black;
  text-align: center;
}

.nav-links {
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-btn {
  white-space: normal;
}

.MuiButton-label {
  text-shadow: 2px 4px 4px black;
}

.MuiButton-label:hover {
  color: aqua;
  text-shadow: none;
}

