.header-hyperlink {
  text-decoration: none;
}

.intro {
  color: white;
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}

.intro p {
  text-align: center;
  font-size: 1.25rem;
  line-height: 1.5rem;
}

/* Slideshow container  */

.slideshow-container {
  max-width: 1000px;
  height: 10rem;
  position: relative;
  margin: 8rem auto;
}

/* Hide the images by default */
.mySlides {
  display: none;
}

.mySlides p:first-child {
  width: 100%;
  height: auto;
  border-radius: 3px;
  font-family: 'Times New Roman', Times, serif;
  font-size: 2rem;
  font-style: italic;
  text-align: center;
  padding: 0;
}

.quote-name {
  float: right;
  margin-right: 1rem;
}

/* Fading animation */
.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

/* projects page */

.projects {
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(400px, 2fr)); */
  grid-template-columns: 50% 50%;
  gap: 3rem;
  margin: 3rem 0 3rem 4rem;
}

.project {
  width: 80%;
  height: 100%;
  margin: 1rem;
}

.project-img-div {
  width: 100%;
}

.project-img {
  width: 100%;
  /* min-width: 100%; */
  height: 30rem;
  border-radius: 4px;
}

.project-text-card {
  text-align: center;
  width: 100%;
  border-radius: 5px;
  font-size: 1.25rem;
}

.website-link h2 {
  padding-bottom: 30px;
}

.website-link h2:hover {
  color: aqua;
}

.github-link {
  font-size: 3rem;
  padding: 1rem;
}

.github-link > a > svg > path:hover {
  color: aqua;
}

.section-title {
  padding-left: 50px;
}

/* contact form */

.contact-div {
  padding-bottom: 100px;
  /* background-color: #00667a; */
  width: 90%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-right: auto;
  margin-left: auto;
}

.contact-me-header {
  font-family: 'Poppins', sans-serif, 'arial';
  font-weight: 600;
  font-size: 45px;
  text-align: center;
  text-shadow: 2px 4px 4px black;
}

h4 {
  font-family: 'Roboto', sans-serif, 'arial';
  font-weight: 400;
  font-size: 20px;
  color: #9b9b9b;
  line-height: 1.5;
}

