/* ///// inputs /////*/

input:focus ~ label,
textarea:focus ~ label,
input:valid ~ label,
textarea:valid ~ label {
  font-size: 1em;
  /* color: #999; */
  top: -5px;
  -webkit-transition: all 0.225s ease;
  transition: all 0.225s ease;
}

::placeholder {
  font-style: italic;
  color: #999;
}

.styled-input {
  float: left;
  width: 293px;
  margin: 1rem 0;
  position: relative;
  border-radius: 4px;
}

.styled-input label {
  color: #999;
  padding: 1.3rem 30px 1rem 30px;
  position: absolute;
  top: 10px;
  left: 0;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  pointer-events: none;
}

.styled-input.wide {
  width: 650px;
  max-width: 100%;
}

input,
textarea {
  padding: 30px;
  border: 0;
  height: 2em;
  width: 90%;
  font-size: 1.5rem;
  background-color: white;
  color: black;
  border-radius: 4px;
  margin: 1rem;
}

input:focus,
textarea:focus {
  outline: 0;
}

input:focus ~ span,
textarea:focus ~ span {
  width: 100%;
  -webkit-transition: all 0.075s ease;
  transition: all 0.075s ease;
}

textarea {
  width: 95%;
  min-height: 15em;
}

.input-container {
  width: 650px;
  max-width: 100%;
  margin: 20px auto 25px auto;
}

.submit-btn {
  /* float: right; */
  width: 12rem;
  margin-top: 15px;
  padding: 7px 35px;
  border-radius: 4px;
  display: inline-block;
  background-color: #4b8cfb;
  color: white;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.06), 0 2px 10px 0 rgba(0, 0, 0, 0.07);
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}

.submit-btn:hover {
  transform: translateY(1px);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3), 0 1px 1px 0 rgba(0, 0, 0, 0.09);
}

input[type='checkbox'] + label {
  color: #ccc;
  font-style: italic;
}

input[type='checkbox']:checked + label {
  color: #f00;
  font-style: normal;
}

div.info-and-form {
  background-color: white;
}

/* resume page */

.resume-link {
  font-size: 3rem;
  text-align: center;
  padding: 3rem;
}

.resume-link a {
  text-decoration: none;
  font-style: italic;
}

.resume-link a:hover {
  color: aqua;
  letter-spacing: 0.5rem;
}

.resume-pages {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* .resume-pages img {
  display: block;
  margin-left: auto;
  margin-right: auto;
} */

/* footer */
.footer {
  display: flex;
  justify-content: center;
  justify-content: space-between;
  /* margin-top: 2rem; */
  padding: 2rem;
  bottom: 0;
  left: 0;
  width: 100%;
  /* box-shadow: 0 3px 10px rgb(0 0 0 / 0.7); */
  text-shadow: 2px 4px 4px black;
}

.footer-icons {
  display: flex;
  flex: wrap;
  justify-content: space-evenly;
  margin-top: 1.5rem;
}

.footer-icons a {
  padding: 2rem;
}

.footer-icons a svg:hover {
  fill: aqua;
  cursor: pointer;
}

.copyright {
  /* text-align: right; */
  display: flex;
  justify-content: space-between;
  color: white;
  font-size: 2rem;
}

.copyright-icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.copyright h4 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 1.7rem;
  padding-left: 0.5rem;
  color: white;
}

/* media queries */
@media (max-width: 3500px) {
  .projects {
    display: flex;
  }
  .project-img {
    width: 100%;
    height: 30rem;
  }
}

@media (max-width: 2799px) {
  .projects {
    display: grid;
  }
  .project-img {
    width: 100%;
    height: 40rem;
  }
}

@media (max-width: 2400px) {
  .project-img {
    width: 90%;
    height: 35rem;
  }
}

@media (max-width: 2200px) {
  .project-img {
    width: 100%;
  }
}

@media (max-width: 1900px) {
  .project-img {
    height: 30rem;
  }
}

@media (max-width: 1750px) {
  .project-img {
    height: 25rem;
  }
}

@media (max-width: 1400px) {
  .project-img {
    height: 20rem;
  }
}

@media (max-width: 1260px) {
  .projects {
    margin-left: 2rem;
  }
}
@media (max-width: 1160px) {
  .project-img {
    height: 18rem;
  }
}
@media (max-with: 1100px) {
  .project-img {
    height: 15rem;
  }
}

@media (max-width: 1024px) {
  .projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem;
  }
  .project {
    margin: 0;
  }
  .project-img {
    height: auto;
  }
  .resume-pages img {
    width: 95%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (max-width: 768px) {
  .flex-wrapper {
    background-size: 200% 100%;
  }
  .header {
    margin: 0;
  }
  p.about-paragraph {
    margin-top: 0;
    padding-top: 0;
  }
  .slideshow-container {
    margin-top: 5rem;
    margin-bottom: 0;
  }
  .mySlides p:first-child {
    font-size: 1.25rem;
  }
  .submit-btn {
    float: right;
    text-align: center;
  }
  .projects {
    margin: 0;
  }
  .project {
    width: 100%;
  }
  .styled-input {
    width: 100%;
  }
  .footer {
    justify-content: space-between;
  }
}

@media (max-width: 576px) {
  .header {
    font-size: 3rem;
  }
  .nav-btn {
    align-items: flex-end;
  }
  p.about-paragraph {
    font-size: 1rem;
  }
  .email-link {
    font-size: 20px;
  }
  .name-input,
  .email-input,
  textarea {
    width: 94%;
  }
  .submit-btn {
    width: 30%;
  }
  .footer {
    justify-content: space-around;
    padding: 1rem;
  }
  .footer-icons {
    /* flex-direction: column; */
    font-size: 2rem;
    /* justify-content: space-around; */
  }
  .footer-icons a {
    padding: 1rem;
  }
  .quote-name {
    font-size: 0.75rem;
  }
}

@media (max-width: 424px) {
  .projects {
    margin: 0;
  }
  .submit-btn {
    /* width: 70%; */
    float: right;
  }
  .textarea {
    width: 90%;
  }
  .contact-form {
    width: 97%;
  }
}

@media (max-width: 320px) {
  .MuiButton-label {
    font-size: 0.75rem;
  }
  .nav-btn a {
    font-size: 0.8rem;
  }
  .input,
  textarea {
    height: auto;
    width: 90%;
    font-size: 1.25rem;
  }
  .contact-me-header {
    font-size: 40px;
  }
  .name-input,
  .email-input,
  textarea {
    width: 92%;
  }
  .submit-btn {
    width: 40%;
    margin-right: 1rem;
  }
  .copyright svg {
    height: 1.5rem;
  }
  .copyright h4 {
    font-size: 1rem;
    margin-bottom: 1.25rem;
    padding-left: 0.25rem;
  }
}

@media (max-width: 281px) {
  .MuiButton-label {
    font-size: 0.5rem;
  }
}
